#loadingPage {
  /* position: relative; */
  width: 100%;
  max-width: var(--max-width);
  margin: auto;
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#loadingPageLottie {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  margin: auto;
  width: 80%;
}
#fillPageColor {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0);
  transform-origin: center;
  border-radius: 50%;
  background-color: var(--main-color);
  width: 5px;
  height: 5px;
  transition: all 300ms linear;
}
#laodingTxt {
  position: absolute;
  left: 50%;
  bottom: 10%;
  transform: translate(-50%);
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--main-color);
}
#svg1 {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transform-origin: center;
  width: 200px;
  height: 200px;
  stroke-width: 5px;
  stroke-linecap: round;
  stroke: blueviolet;
  fill: none;
  animation: rotateSvg 500ms linear 0s infinite normal both, resizeMiddleSvg 2s linear 4s infinite alternate backwards;
}
