@keyframes rotateSvg {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
@keyframes resizeMiddleSvg {
  0%,
  90% {
    stroke-width: 5;
  }
  90%,
  100% {
    stroke-width: 10;
  }
}
@keyframes svg2 {
  from {
    width: 200px;
    height: 200px;
  }
  to {
    width: calc(200px - (1 * 15px));
    height: calc(200px - (1 * 15px));
  }
}
