/************** Welcome Page **************/

#welcomePagesContainer {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  white-space: nowrap;
  overflow: hidden;
}
.welcomePages {
  position: relative;
  display: inline-block;
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  vertical-align: top;
  overflow: hidden;
  padding: 0px;
  margin: 0px;
  background-color: var(--main-color);
  background: linear-gradient(0deg, var(--second-color), var(--main-color) 100%);
}
#firstPage {
  background-color: initial;
  background: initial;
  clip-path: circle(0% at 50% 50%);
  transition: clip-path 1s;
}
.welcomeGrade {
  position: absolute;
  bottom: 0%;
  left: 0%;
  width: 100%;
  height: 30%;
  background: linear-gradient(0deg, rgb(0, 0, 0, 0.8), rgb(0, 0, 0, 0) 100%);
}
#welcomeVideoFilter {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.1);
}
.welcomePageTitle {
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translate(-50%);
  text-align: center;
  font-size: calc(40px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: white;
}
.welcomePageSubTitle {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%);
  text-align: center;
  font-size: calc(20px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: white;
}
#welcomeVideo {
  position: absolute;
  z-index: -100;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto !important;
  height: 0;
  transform: translate(-50%, -50%);
}
#genderContainer {
  position: absolute;
  top: 60%;
  left: 50%;
  width: 45%;
  transform: translate(-50%, -50%);
}
#genderContainer p {
  width: 100%;
  padding: 10px 0px;
  margin: 0px;
  margin-bottom: 30px;
  text-align: center;
  font-size: calc(20px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--main-color);
  background-color: white;
  border-radius: 50px;
  box-shadow: 1px 1px 7px 0px rgba(0, 0, 0, 0.55);
  cursor: pointer;
}
#genderContainer p:hover {
  color: black;
}
.welcomeInputWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: absolute;
  top: 12%;
  left: 50%;
  transform: translate(-50%);
  width: 90%;
  height: 60%;
  background-color: white;
  border-radius: 20px;
  padding-bottom: 10px;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.55);
}
.welcomeInputContainer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
  width: 90%;
  text-align: center;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  margin: 0px auto;
}
.welcomeInputContainer input {
  display: block;
  width: 100%;
  margin: 0px auto;
  text-align: center;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  border: none;
  border-radius: 0px;
  border-bottom: solid 1.5px var(--main-color);
  color: var(--main-color);
}
.welcomeInputContainer > label {
  width: 100%;
  margin: 0px auto;
  color: var(--main-color);
  text-align: right;
  transition: all 300ms ease;
}
.welcomeInputContainer input:valid + div {
  right: 100%;
  opacity: 1;
}
.unitsContainer {
  position: absolute;
  right: 50%;
  top: 45%;
  opacity: 0;
  transform: translate(100%);
  pointer-events: none;
  transition: all 300ms ease;
}
.unitsContainer img {
  width: 20px;
  height: 20px;
  vertical-align: middle;
}
.unitsContainer p {
  display: inline-block;
  color: var(--second-color);
  font-size: 12px;
  margin: 0;
  margin-left: 5px;
  vertical-align: middle;
}
#activityWrapper {
  position: absolute;
  top: 12%;
  left: 50%;
  transform: translate(-50%);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background-color: white;
  border-radius: 20px;
  height: 60%;
  width: 90%;
  margin: auto;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.55);
}
#activityWrapper div:last-child {
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  border-bottom: none;
}
#activityWrapper div:first-child {
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
}
#activityTitle {
  position: absolute;
  top: 15px;
  left: 50%;
  transform: translate(-50%);
  margin: 0px;
  text-align: center;
  font-size: calc(20px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: white;
}
.activityContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  border-bottom: solid 1px rgb(179, 179, 179);
  box-sizing: border-box;
  cursor: pointer;
  transition: 200ms;
}
.activityContainer:active {
  background-color: #b2cfff;
}
.activityContainer p:nth-child(1) {
  margin: 0px auto;
  text-align: center;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--main-color);
  pointer-events: none;
}
.activityContainer p:nth-child(2) {
  margin: 0px auto;
  text-align: center;
  font-size: calc(13.5px + 6 * ((var(--vw) - 320px) / 680));
  color: black;
  pointer-events: none;
}
.welcomeNextButtons {
  position: absolute;
  bottom: 8%;
  left: 50%;
  transform: translate(-50%);
  width: 70%;
  text-align: center;
  font-size: calc(20px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--main-color);
  background-color: white;
  padding: 5px 0px;
  border-radius: 50px;
  border: none;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.55);
  cursor: pointer;
}
.welcomeNextButtons:hover {
  color: black;
}
.welcomeBackButtons {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 30px;
  height: 30px;
  transform: rotate(180deg);
  font-size: calc(20px + 6 * ((var(--vw) - 320px) / 680));
  fill: white;
  cursor: pointer;
}
.welcomeBackButtons:hover {
  fill: #2b2b2b;
}
#loadingLottie {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: auto;
  width: 60%;
}
