/************** Summary **************/

#summaryPage {
    width: 100%;
    max-width: var(--max-width);
    min-width: 320px;
    margin: auto;
    opacity: 0;
    transition: opacity 250ms;
    transition-delay: 100ms;
  }
  #summaryPageHeader {
    position: sticky;
    top: 0;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 70px;
    background-color: var(--main-color);
  }
  #summaryPageHeaderTitle {
    color: white;
    margin: 0;
    margin-right: 30px;
    font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
  }
  /*kcal chart card*/
  #summaryLastWeekKcalContainer {
    background-color: var(--background-white);
    padding: 20px;
    padding-bottom: 50px;
    margin: 15px;
    border-radius: 10px;
    box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  }
  #summaryLastWeekKcalTitle {
    text-align: center;
    font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    color: var(--text-kinda-black);
    margin: 0;
    margin-bottom: 50px;
  }
  #summaryLastWeekKcalNumContainer {
    display: inline-flex;
    flex-direction: column;
    vertical-align: bottom;
    justify-content: space-between;
    align-items: center;
    width: 13%;
    height: 217px;
  }
  #summaryLastWeekKcalNumContainer p {
    margin: 0;
    color: var(--text-kinda-black);
    font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  }
  #summaryLastWeekKcalNumContainer p:nth-child(1) {
    margin-top: -5px;
  }
  #summaryLastWeekKcalChartContainer {
    position: relative;
    display: inline-flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 85%;
    height: 200px;
    border-bottom: solid 1px var(--second-color);
    border-top: solid 1px var(--second-color);
    box-sizing: border-box;
    margin-right: 2%;
    z-index: 0;
  }
  .Kcalcharts {
    position: relative;
    height: 0%;
    max-height: 115%;
    min-height: 0.5%;
    width: 20px;
    background-color: var(--main-color);
    transition: height 350ms ease-out;
    transition-delay: 0.3s;
  }
  .Kcalcharts p {
    position: absolute;
    bottom: 0px;
    left: 50%;
    margin: 0;
    transform: translate(-50%, 100%);
    font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
    color: var(--text-kinda-black);
  }
  #chartMiddleLine {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: var(--second-color);
    z-index: -1;
  }
  /*Nutritions chart card*/
  #summaryLastWeekNutritionsContainer {
    background-color: var(--background-white);
    padding: 20px;
    padding-bottom: 50px;
    margin: 15px;
    border-radius: 10px;
    box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  }
  #summaryLastWeekNutritionsTitle {
    text-align: center;
    font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    color: var(--text-kinda-black);
    margin: 0;
    margin-bottom: 50px;
  }
  #summaryLastWeekNutritionsNumContainer {
    display: inline-flex;
    flex-direction: column;
    vertical-align: bottom;
    justify-content: space-between;
    align-items: center;
    width: 13%;
    height: 217px;
  }
  #summaryLastWeekNutritionsNumContainer p {
    margin: 0;
    color: var(--text-kinda-black);
    font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  }
  #summaryLastWeekNutritionsNumContainer p:nth-child(1) {
    margin-top: -5px;
  }
  #summaryLastWeekNutritionsChartContainer {
    position: relative;
    display: inline-flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 85%;
    height: 200px;
    border-bottom: solid 1px var(--second-color);
    border-top: solid 1px var(--second-color);
    box-sizing: border-box;
    margin-right: 2%;
    z-index: 0;
  }
  .Nutritionscharts {
    height: 100%;
    width: 20px;
    transform: rotate(180deg);
  }
  .Nutritionscharts div {
    width: 20px;
    height: 0;
    transition: height 350ms ease-out;
    transition-delay: 0.3s;
  }
  .Nutritionscharts div:nth-child(1) {
    background: linear-gradient(#f09819, #ff512f);
    background-color: #424242;
    min-height: 0.5%;
  }
  .Nutritionscharts div:nth-child(2) {
    background: linear-gradient(#da22ff, #9733ee);
    background-color: #82827c;
  }
  .Nutritionscharts div:nth-child(3) {
    background: linear-gradient(#4364f7, #0052d4);
    background-color: #b5b5ac;
  }
  .Nutritionscharts p {
    position: absolute;
    bottom: 0px;
    left: 50%;
    margin: 0;
    transform: translate(-50%, 100%);
    font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
  }
  #chartMiddleLine {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: var(--second-color);
    z-index: -1;
  }
  #summaryLastWeekNutritionsListContainer {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 25px;
  }
  #summaryLastWeekNutritionsListContainer p {
    text-align: center;
    font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    color: var(--text-kinda-black);
    margin: 0;
  }
  #summaryLastWeekNutritionsListContainer p:nth-child(1)::before {
    content: "";
    display: inline-block;
    width: 10px;
    height: 10px;
    margin-left: 5px;
    border-radius: 2px;
    background: linear-gradient(#ff512f, #f09819);
    background-color: #424242;
  }
  #summaryLastWeekNutritionsListContainer p:nth-child(2)::before {
    content: "";
    display: inline-block;
    width: 10px;
    height: 10px;
    margin-left: 5px;
    border-radius: 2px;
    background: linear-gradient(#9733ee, #da22ff);
    background-color: #82827c;
  }
  #summaryLastWeekNutritionsListContainer p:nth-child(3)::before {
    content: "";
    display: inline-block;
    width: 10px;
    height: 10px;
    margin-left: 5px;
    border-radius: 2px;
    background: linear-gradient(#0052d4, #4364f7, #6fb1fc);
    background-color: #b5b5ac;
  }
  /*by Arithmetic card*/
