/************** MainPage Loading **************/

@keyframes loadingPreivew {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.4;
  }
  100% {
    opacity: 1;
  }
}
.txtLoadingAnim {
  background-color: rgba(255, 255, 255, 0.3);
  animation: loadingPreivew 1.5s ease-in-out 0.5s infinite;
  border-radius: 10px;
}
.tipsLinesLoading {
  color: rgb(0, 0, 0, 0);
  background-color: rgba(255, 255, 255, 0.3);
  width: 95%;
  height: 22px;
  margin: 8px auto;
  border-radius: 10px;
}
