:root {
  --vw: 100vw;

  --max-width: 580px;
  --main-color: #099a97;
  --second-color: #15cda8;

  --text-kinda-black: #2b2b2b;
  --text-white: white;
  --text-gray: #646464;

  --background-white: white;
  --background-gray: #f5f5f5;
  --background-kinda-white: #fafaf5;

  --select-background-image: url(../assets/down-arrow-black.svg);
}
@media (max-width: 780px) {
  :root {
    --max-width: 100vw;
  }
}
body {
  margin: 0;
  background-color: var(--background-gray);
  direction: rtl;
  font-family: "Cairo", sans-serif;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  overscroll-behavior-y: contain;
  letter-spacing: -0.5px;
}
input {
  -webkit-appearance: none;
  appearance: none;
}
select {
  -webkit-appearance: none;
  appearance: none;
  background-image: var(--select-background-image);
  background-repeat: no-repeat;
  background-size: 15px 15px;
  background-position: 3%;
  letter-spacing: -0.5px;
}
select,
button {
  font-family: "Cairo", sans-serif;
  cursor: pointer;
}
input {
  font-family: "Cairo", sans-serif;
  cursor: auto;
}
select:focus,
input:focus,
button:focus {
  outline: none;
}

/************** Firebaseui **************/

#firebaseui-auth-container {
  position: absolute;
  bottom: 20%;
  left: 50%;
  transform: translate(-50%);
}
.firebaseui-idp-text {
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  font-family: "Cairo", sans-serif;
}
.firebaseui-idp-button,
.firebaseui-tenant-button {
  max-width: initial;
  border-radius: 50px;
  padding: 8px 25px;
}
.firebaseui-container {
  max-width: initial;
}
#accountchooser-iframe {
  display: none;
}


/************** Snackbar **************/

.snackbar {
  position: fixed;
  bottom: 100px;
  left: 50%;
  right: auto;
  transform: translate(-50%) scale(0);
  display: flex;
  max-width: var(--max-width);
  padding: 6px 16px;
  font-size: 1rem;
  color: #fff;
  font-weight: 400;
  line-height: 1.43;
  border-radius: 4px;
  opacity: 0;
  z-index: 1000;
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 6px 10px 0px rgba(0, 0, 0, 0.14), 0px 1px 18px 0px rgba(0, 0, 0, 0.12);
  transition: opacity 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, transform 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}
.alertIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.9;
  padding: 7px 0;
  font-size: 22px;
  margin-left: 12px;
}
.alertIcon svg {
  display: none;
  fill: currentColor;
  width: 1em;
  height: 1em;
  font-size: 1.5rem;
  transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  flex-shrink: 0;
  line-height: 1.43;
  user-select: none;
}
.alertMessage {
  white-space: nowrap;
  padding: 8px 0;
  line-height: 1.43;
  font-size: 1rem;
  margin: 0px;
}

