/************** MealCard **************/

.mealCardContainer {
    border-bottom: solid 2px var(--background-gray);
    cursor: pointer;
    padding-bottom: 10px;
    padding-top: 10px;
    background-color: var(--background-kinda-white);
    display: flex;
    align-items: center;
    text-align: right;
  }
  .mealCardContainer:hover {
    background-color: var(--second-color);
  }
  .mealCardContainer:hover .mealCardInfoContainer p {
    color: white;
  }
  .mealCardIcon {
    background-image: url(../assets/mealIcon.png);
    background-size: contain;
    background-repeat: no-repeat;
    width: 40px;
    height: 40px;
    display: inline-block;
    margin-left: 20px;
    margin-right: 20px;
  }
  .mealCardName {
    margin: 0;
    font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    color: var(--text-kinda-black);
  }
  .mealCardIngredients {
    margin: 0;
    font-size: calc(10px + 6 * ((var(--vw) - 320px) / 680));
    color: var(--text-gray);
  }
  .mealCardKcal {
    margin: 0;
    font-size: calc(11px + 6 * ((var(--vw) - 320px) / 680));
    color: var(--text-kinda-black);
  }
  