/************** MainPage **************/

#MainPage {
    position: relative;
    margin: auto;
    width: 100%;
    min-width: 320px;
    max-width: var(--max-width);
    padding-bottom: 95px;
    text-align: center;
    opacity: 0;
    transition: opacity 250ms;
    transition-delay: 100ms;
  }
  #header {
    position: sticky;
    z-index: 1;
  }
  #Gradient1 stop:nth-child(1) {
    stop-color: var(--main-color);
  }
  #Gradient1 stop:nth-child(2) {
    stop-color: var(--second-color);
  }
  #title {
    position: fixed;
    top: 25px;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    color: white;
    margin: 0;
    margin-top: -5px;
    font-size: calc(20px + 6 * ((var(--vw) - 320px) / 680));
    text-shadow: 1.5px 1.5px 1px rgba(0, 0, 0, 0.6);
  }
  .remainValues {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    position: absolute;
    top: 37%;
    transform: translate(0, -50%);
  }
  .caloriesRemain,
  .caloriesEaten {
    margin: auto;
    text-align: center;
    font-weight: bold;
    font-size: calc(12px + 6 * ((var(--vw) - 320px) / 680));
    color: white;
    width: 100px;
  }
  .caloriesRemain p,
  .caloriesEaten p {
    margin: 0px;
    margin-bottom: 0px;
    font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  }
  .caloriesRemain p:nth-child(1),
  .caloriesEaten p:nth-child(1) {
    font-family: sans-serif;
    font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  }
  #macrosPercent {
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: space-around;
    color: white;
    font-size: calc(12px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
  }
  .macrosPercentContainer {
    position: relative;
    width: 29%;
    height: 5px;
    border-radius: 0.5px;
    background-color: rgb(250, 250, 245, 0.5);
  }
  .macrosPercentLine {
    position: absolute;
    right: 0px;
    top: 0px;
    height: 5px;
    width: 0;
    max-width: 100%;
    border-radius: 0.5px;
    background-image: linear-gradient(-225deg, #fffeff 0%, #d7fffe 100%);
    transition: width 500ms ease;
  }
  .macroName {
    position: absolute;
    top: -5px;
    left: 50%;
    transform: translate(-50%, -100%);
    margin: 0px;
    font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  }
  .percentNum {
    margin: 0;
    margin-top: 10px;
    font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  }
  .percentNum span {
    margin: 3px;
  }
  @keyframes arrowDailyDetailsAnim {
    20%, 100% {
      bottom: 20px;
      transform: scale(1);
    }
    0% {
      bottom: 20px;
      transform: scale(1);
    }
    10% {
      bottom: 5px;
      transform: scale(0.6);
    }
  }
  #arrowDailyDetails {
    position: absolute;
    bottom: 20px;
    left: calc(50% - 20px);
    width: 40px;
    height: 40px;
    fill: white;
    cursor: pointer;
    animation: arrowDailyDetailsAnim 5s ease 1s infinite normal forwards;
  }
  #arrowDailyDetails:hover {
    fill: black;
  }
  #addButton {
    position: sticky;
    margin-left: 15px;
    bottom: 100px;
    float: left;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    z-index: 3;
    cursor: pointer;
    background-color: var(--main-color);
    box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
  }
  #addButton svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  @keyframes PulsAnimation {
    40%, 100% {
      opacity: 0;
    }
    0% {
      opacity: 1;
    }
    30% {
      opacity: 0;
      transform: scale(1.4);
    }
  }
  #addButtonPulsAnimation1 {
    animation: PulsAnimation 4s ease 1s infinite normal forwards;
  }
  #addButtonPulsAnimation2 {
    animation: PulsAnimation 4s ease 1.3s infinite normal forwards;
  }
  #addButtonPulsAnimation1,
  #addButtonPulsAnimation2 {
    position: absolute;
    top: clac(50% - 27.5px);
    left: clac(50% - 27.5px);
    width: 55px;
    height: 55px;
    border: solid 1.5px var(--main-color);
    border-radius: 50%;
    box-sizing: border-box;
    pointer-events: none;
  }
  .circleButton {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    z-index: 3;
    cursor: pointer;
  }
  .floatButton {
    position: fixed;
    bottom: 60px;
    transform: translate(-50%, -50%) scale(0);
    text-align: center;
    background-color: var(--background-white);
    -webkit-box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
    -moz-box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
    box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
    display: none;
  }
  .floatButton p {
    position: absolute;
    left: 50%;
    top: 60px;
    transform: translate(-50%);
    margin: 0;
    color: var(--text-kinda-black);
    font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  }
  .floatButton img {
    margin-top: 5px;
    width: 50px;
  }
  #addFoodBG {
    position: fixed;
    top: 0;
    left: 0;
    margin: 0;
    width: 100vw;
    height: 100vh;
    z-index: 3;
    background-color: var(--background-kinda-white);
    opacity: 0.95;
    display: none;
    opacity: 0;
    touch-action: none;
  }
  #dateSelector {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: var(--max-width);
    min-width: 320px;
  }
  #dateSelector p {
    font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
    color: var(--text-gray);
    direction: ltr;
    margin: 20px 0;
    transition: margin 500ms;
  }
  .dateArrow {
    padding: 10px;
    width: 20px;
    height: 20px;
    cursor: pointer;
    fill: var(--main-color);
  }
  #tipsContainer {
    position: relative;
    margin: 10px auto;
    padding-top: 10px;
    width: 97%;
    background-color: azure;
    border-radius: 5px;
    box-shadow: 0px 5px 10px -7px rgba(0, 0, 0, 0.55);
    background: linear-gradient(190deg, var(--main-color) 0%, var(--second-color) 100%);
    transition: all 400ms;
    overflow: hidden;
    text-overflow: clip;
  }
  #tipsContainer svg {
    position: absolute;
    width: 15px;
    height: 15px;
    right: 15px;
    top: 15px;
    fill: white;
    cursor: pointer;
  }
  #tipsContainer svg:hover {
    fill: var(--text-kinda-black);
  }
  #tipsContainer p:nth-child(2) {
    margin: 0;
    font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
    color: wheat;
    font-weight: bold;
  }
  #tipsContainer p:nth-child(3) {
    margin: auto;
    text-align: justify;
    padding: 0px 15px 15px 15px;
    color: white;
    font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
    transition: all 400ms;
  }
  #diaryContainer {
    white-space: nowrap;
    overflow-x: hidden;
    width: 100%;
    margin: auto;
    /* padding-right: 7px;
    padding-left: 7px; */
    padding-bottom: 60px;
    will-change: scroll-position;
  }
  @media (max-width: 400px) {
    .floatButton img {
      margin-top: 8px;
      width: 45px;
    }
    .circleButton {
      width: 55px;
      height: 55px;
    }
    #addButton svg {
      width: 18px;
      height: 18px;
    }
    #addButton {
      width: 50px;
      height: 50px;
    }
    #addButtonPulsAnimation1,
    #addButtonPulsAnimation2 {
      top: clac(50% - 25px);
      left: clac(50% - 25px);
      width: 50px;
      height: 50px;
    }
    .dateArrow {
      width: 18px;
      height: 18px;
    }
  }
  