/************** diary **************/

#pageBody {
  display: inline-block;
  vertical-align: top;
  width: calc(100% - 14px);
  max-width: var(--max-width);
  min-width: 306px;
  padding: 0px 7px;
}
.mealPopUp {
  position: absolute;
  top: 42px;
  left: 20px;
  background-color: var(--background-gray);
  border-radius: 5px;
  box-shadow: 5px 5px 7px -6px rgba(0, 0, 0, 0.55);
  z-index: 1;
  transform: scale(0);
  transform-origin: 0 0;
  transition: transform 200ms;
}
.mealPopUp p {
  padding: 10px 30px;
  margin: 0;
  color: var(--text-kinda-black);
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  text-align: center;
  cursor: pointer;
}
.mealPopUp p:hover {
  color: var(--main-color);
}
.mealName {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--background-white);
  color: #1f1f1e;
  width: 100%;
  margin-top: 20px;
  vertical-align: middle;
  text-align: right;
  box-sizing: border-box;
  -webkit-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  -moz-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
.mealName h3 {
  margin: 0;
  padding: 8px 20px;
  font-weight: normal;
  color: var(--text-kinda-black);
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
}
.mealName svg {
  display: none;
  width: 18px;
  height: 18px;
  fill: var(--text-gray);
  margin-left: 2%;
  cursor: pointer;
}
.mealName svg:hover {
  fill: var(--main-color);
}
.mealFoods {
  margin: auto;
  color: #1f1f1e;
  box-sizing: border-box;
  background-color: var(--background-kinda-white);
  -webkit-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  -moz-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
.addYourFoodText {
  margin: 0;
  color: var(--text-kinda-black);
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
}
.mealFoods img {
  width: 50px;
}
.mealFoodsImgP {
  padding: 5px 0;
  cursor: pointer;
}
.mealKcalSum {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--background-white);
}
.mealKcalSum p {
  margin: 0;
  padding: 8px 20px;
  font-weight: normal;
  color: var(--text-gray);
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  text-align: right;
}
.mealEaten {
  position: relative;
  width: 20px;
  height: 20px;
  margin: 0px 15px;
  border: solid 1px lightgray;
  border-radius: 3px;
  background-color: var(--background-kinda-white);
  box-sizing: border-box;
  cursor: pointer;
  transition: all 350ms;
}
.mealEaten svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: lightgray;
  width: 18px;
  height: 18px;
  transition: all 350ms;
}
@media (max-width: 400px) {
  .mealFoods img {
    width: 45px;
  }
  .mealName svg {
    width: 18px;
    height: 18px;
  }
}
