/************** Settings **************/

#settingsPage {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  padding-bottom: 100px;
  opacity: 0;
  transition: opacity 250ms;
  transition-delay: 100ms;
}
#settingsPageHeader {
  position: sticky;
  top: 0;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  background-color: var(--main-color);
}
#UserContainer {
  position: relative;
  background-color: var(--background-white);
  margin: 0 7px;
  margin-top: 30px;
  padding: 20px;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
#userPic {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  margin: auto;
  background-image: url(../assets/userBG.png);
  background-size: cover;
  background-repeat: no-repeat;
}
#userName {
  text-align: center;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--text-kinda-black);
}
#logOut {
  display: block;
  width: 50%;
  border: none;
  border-radius: 10px;
  margin: auto;
  padding: 5px 0px;
  font-weight: bold;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  color: white;
  background-color: var(--main-color);
  background: linear-gradient(90deg, var(--main-color) 0%, var(--second-color) 75%);
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  cursor: pointer;
}
#settingsPageHeaderTitle {
  color: white;
  margin: 0;
  margin-right: 30px;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
}
.containerTitles {
